import axios from "axios";
import download from "downloadjs";

const convertcsvToJSON = async (formData, fileName) => {
  try {
    console.log("test");
    const url = "http://localhost:3001/convert/csv/json";
    // const formData = new FormData();
    // formData.append('file', file)
    const config = {
      headers: {
        "content-type": "multipart/form-data",
      },
    };
    const event = await axios.post(url, formData, config);
    const json = event.data;
    var fileToSave = new Blob([JSON.stringify(json, undefined, 2)], {
      type: "application/json",
    });
    download(fileToSave, `${fileName}.json`);
  } catch (e) {
    console.log(e);
    if (e.response) {
      throw new Error(e.response.data.message);
    } else {
      throw new Error("Please refresh page & try again.");
    }
  }
};

const convertPDFToImage = async (formData, fileName, imageType) => {
  try {
    const url = "http://localhost:3001/convert/pdf/img/" + imageType;
    const config = {
      headers: {
        "content-type": "multipart/form-data",
      },
      responseType: "arraybuffer",
    };
    const res = await axios.post(url, formData, config);
    download(res.data, `${fileName}.zip`);
  } catch (e) {
    console.log(e);
    if (e.response) {
      throw new Error(e.response.data.message);
    } else {
      throw new Error("Please refresh page & try again.");
    }
  }
};

const convertImageToPDF = async (formData, fileName) => {
  try {
    console.log(formData.get("file"));
    const url = "http://localhost:3001/convert/img/pdf";
    const config = {
      headers: {
        "content-type": "multipart/form-data",
      },
      responseType: "blob", // had to add this one here
    };
    const res = await axios.post(url, formData, config);
    const content = res.headers["content-type"];
    download(res.data, `${fileName}.pdf`, content);
  } catch (e) {
    console.log(e);
    if (e.response) {
      throw new Error(e.response.data.message);
    } else {
      throw new Error("Please refresh page & try again.");
    }
  }
};

const convertImageToImage = async (
  formData,
  fileName,
  inputType,
  outputType
) => {
  try {
    console.log(formData.get("file"));
    const url = `http://localhost:3001/convert/${inputType}/${outputType}`;
    const config = {
      headers: {
        "content-type": "multipart/form-data",
      },
      responseType: "blob", // had to add this one here
    };
    const res = await axios.post(url, formData, config);
    const content = res.headers["content-type"];
    download(res.data, `${fileName}.${outputType}`, content);
  } catch (e) {
    console.log(e);
    if (e.response) {
      throw new Error(e.response.data.message);
    } else {
      throw new Error("Please refresh page & try again.");
    }
  }
};

export {
  convertcsvToJSON,
  convertPDFToImage,
  convertImageToPDF,
  convertImageToImage,
};
