import { useState } from "react";
import { Button } from "react-bootstrap";
import { Row } from "react-bootstrap";
import { Container, Col } from "react-bootstrap";
import { Dropdown } from "react-bootstrap";
import { Form } from "react-bootstrap";
import {
  convertcsvToJSON,
  convertImageToImage,
  convertImageToPDF,
  convertPDFToImage,
} from "../services/convert";
import { Spinner } from "react-bootstrap";
function FileConverter() {
  const [file, setFile] = useState(null); // input file
  const [validOutput, setValidOutput] = useState([]);
  const [format, setFormat] = useState(""); // output file type
  const [converting, setConverting] = useState(false);
  let validTypes = {
    pdf: ["png", "jpg"],
    png: ["jpg", "pdf"],
    jpg: ["png", "pdf"],
    jpeg: ["png", "pdf"],
    csv: ["json"],
  };
  console.log(converting);
  const updateInputFileStatus = (e) => {
    if (e.target.files.length == 0) {
      setFile(null);
      setValidOutput([]);
      setFormat("");
    } else {
      const temp = e.target.files[0];
      setFile(temp);
      const types = temp.name.split(".");
      setValidOutput(validTypes[types[types.length - 1].toLowerCase()]);
      setFormat("");
    }
  };
  const setFileFormat = (e) => {
    setFormat(e.target.value);
  };
  const convert = async (e) => {
    e.preventDefault();
    const formData = new FormData(e.target);
    const types = file.name.split(".");
    const inputFileType = types[types.length - 1].toLowerCase();
    types.pop();
    const outputFileType = format;
    const outputFileName = types.join("");
    setConverting(true);
    if (inputFileType == "csv" && outputFileType == "json") {
      await convertcsvToJSON(formData, outputFileName);
    } else if (inputFileType == "pdf") {
      await convertPDFToImage(formData, outputFileName, outputFileType);
    } else if (
      (inputFileType == "jpg" || inputFileType == "png") &&
      outputFileType == "pdf"
    ) {
      await convertImageToPDF(formData, outputFileName);
    } else {
      await convertImageToImage(
        formData,
        outputFileName,
        inputFileType,
        outputFileType
      );
    }
    setConverting(false);
  };
  return (
    <Form onSubmit={convert}>
      <Container className="pb-3">
        <Row className="converter-box mt-5">
          <Col className="d-flex align-items-center justify-content-center" md>
            <Button
              variant="outline-dark"
              style={{ pointerEvents: "none" }}
              className="w-75"
            >
              {file ? file.name : "No file selected"}
            </Button>
          </Col>
          <Col
            className="d-flex align-items-center justify-content-center mt-2"
            md
          >
            <p>to</p>
          </Col>
          <Col className="d-flex align-items-center justify-content-center" md>
            <Form.Select
              className="output-file-select w-75"
              aria-label="Default select example"
              onChange={setFileFormat.bind(this)}
              name="output_type"
              value={format}
            >
              <option className="w-75">Choose output file type</option>
              {/*
              <option className="w-75" value="pdf">
                pdf
              </option>
              <option className="w-75" value="jpg">
                jpg
              </option>
             */}
              {validOutput &&
                validOutput.map((e, index) => {
                  return (
                    <option key={index} className="w-75" value={e}>
                      {e}
                    </option>
                  );
                })}
            </Form.Select>
          </Col>
        </Row>
        <Row>
          <Col md>
            <Container className="d-flex align-items-center justify-content-center">
              <label
                htmlFor="image_file"
                className="btn btn-secondary mt-5 w-50"
              >
                CHOOSE FILE
              </label>
              <input
                className="file_input"
                id="image_file"
                name="file"
                type="file"
                onChange={updateInputFileStatus.bind(this)}
              ></input>
            </Container>
          </Col>
          <Col md>
            <Container className="d-flex align-items-center justify-content-center">
              <Button
                variant="primary w-50"
                className="mt-md-5 mt-2"
                disabled={file && format ? false : true}
                type="submit"
              >
                CONVERT
              </Button>
            </Container>
          </Col>
        </Row>
        <Container className="d-flex align-items-center justify-content-center">
          {converting && (
            <Spinner animation="border" role="status" className="m-2">
              <span className="visually-hidden">Converting...</span>
            </Spinner>
          )}
        </Container>
      </Container>
    </Form>
  );
}

export default FileConverter;
